<template>
  <div class="wrapper">
      <div>
        <ul>
            <li class="l1" v-for="item in list" :key="item.id">
              <van-checkbox v-model="item.checked" checked-color="#f00"></van-checkbox>
                 <img class="m1" :src="item.pic" alt="">
                 <span>
                     {{ item.name }}
                 </span> 
                 <p>购买数量
                   {{ item.num }}
                 </p>
            </li>
        </ul>

      </div>
     

    <div id="place">
      <van-checkbox v-model="checked" checked-color="#f00">复选框</van-checkbox>
      <p>合计: ￥15</p>
      <p style="display: none;">下单</p>
      <p class="shan" style="">删除</p>
    </div>
  </div>
</template>

<script>
import  { mapState } from "vuex" 
export default {
  components: {},
  props: {},
  data() {
    return {
      checked: false
    }
  },
  watch: {},
  computed: {
      ...mapState({
          // 箭头函数可使代码更简练
          list: state => state.cart,
          t:state=>state.title
     })
  },
  methods: {

  },
  created() {},
  mounted() {}
}
</script>
<style lang="less">
.wrapper {
  overflow: hidden;
}
.l1{
  display: flex;
  span{
    line-height: 1rem;
    margin-left: .1rem;
  }
  p{
    line-height: 1rem;
    margin-left: .2rem;
  }
}
.m1{
  width: 0.85rem;
      height: 0.85rem;
      display: block;
      margin-left: 0.2rem;
      margin-top: 0.2rem;
}
.item {
  height: 100px;
  width: 100%;
  position: relative;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  .del {
    width: 60px;
    height: 100px;
    background: #f00;
    position: absolute;
    right: 0;
  }
}
#place{
    height: 0.68rem;
    border-top: 0.01rem solid #e9e9e9;
    border-bottom: 0.01rem solid #e9e9e9;
    display: flex;
    align-items: center;
    background: white;
    z-index: 1;
    justify-content: space-between;
    padding-left: 0.16rem;
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0.87rem;
    >div{
        width: 2.1rem;
    }
    >p:nth-of-type(1){
        font-size: 0.18rem;
        color: #d00000;
    }
    >p:nth-of-type(2){
        width: 1.66rem;
        height: 0.68rem;
        font-size: 0.2rem;
        color: white;
        text-align: center;
        line-height: 0.68rem;
        background: #d00000;
    }
}
.shan{
        width: 1.66rem;
        height: 0.68rem;
        font-size: 0.2rem;
        color: white;
        text-align: center;
        line-height: 0.68rem;
        background: #d00000;
}
</style>


